import Vue from 'vue';
import Router from 'vue-router';
import Layout from '@/views/layouts';

Vue.use(Router);


/**
 * Loaded router on demand
 */
export const defaultRoutes = [
  {
    path: '/',
    component: Layout,
    redirect: '/article/1',
    name: 'Article',
    meta: {
      title: 'example',
      icon: 'example',
    },
  },
  {
    path: '/article/:id(\\d+)',
    component: Layout,
    name: '文章',
    meta: { title: 'Article', noCache: true },
    hidden: true,
  },
];

export default new Router({
  mode: 'history',
  routes: defaultRoutes,
});



